<template>
  <div class="home full-height">
    <div class="container py-4 full-height">
      <div class="row full-height">
        <div class="col-8 full-height">
          <div ref="sketch" class="full-height">
            <vue-p5 @sketch="sketch"></vue-p5>
          </div>
        </div>
        <div class="col-4">
          <h3>Controls</h3>
          <small>Click on canvas to move</small>
          <Point class="my-2" v-model="p" readonly :size="sketchSize">P</Point>

          <Point class="my-2" v-model="left" :size="sketchSize">Left motor</Point>
          <Point class="my-2" v-model="right" :size="sketchSize">Right motor</Point>

          <div class="card card-body p-2">
            <div class="row">
              <div class="col">
                Left R <span class="badge badge-primary">{{ left.r.toFixed(2) }}</span>
                <input type="range" v-model.number="left.r" min="0" :max="maxR" />
              </div>
              <div class="col">
                Right R <span class="badge badge-primary">{{ right.r.toFixed(2) }}</span>
                <input type="range" v-model.number="right.r" min="0" :max="maxR" />
              </div>
            </div>
          </div>

          <div class="card card-body p-2 my-2">
            <h6>G-Code</h6>
            <small>Only G1 command is supported</small>
            <textarea v-model="gcode" rows="10" class="form-control"></textarea>
            <div class="row my-2">
              <div class="col">
                <a href="#" class="btn btn-primary full-width" @click.prevent="startMovement">Draw</a>
              </div>
              <div class="col">
                <a href="#" class="btn btn-primary full-width" @click.prevent="stopMovement">Stop</a>
              </div>
            </div>
            <div class="row my-2">
              <div class="col">
                Move speed <span class="badge badge-primary">{{ moveSpeed }}</span>
                <input type="range" v-model.number="moveSpeed" min="100" max="1000" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/// <reference path="../p5.d.ts" />
import VueP5 from "vue-p5"
import Point from "@/components/Point.vue"
import _ from "lodash"

const DEFAULT_POINT_SIZE = 10

function distance(p1, p2) {
  const v = direction(p1, p2)
  return Math.sqrt(v.x * v.x + v.y * v.y)
}

function direction(p1, p2) {
  const dx = p2.x - p1.x
  const dy = p2.y - p1.y
  return { x: dx, y: dy }
}

/**
 * Callback to calculate number based on t param
 *
 * @callback functionT
 * @param {Number} t <0.0, 1.0>
 */
/**
 * @typedef {Object} MoveFunction
 * @property {functionT} x calculates x based on t param
 * @property {functionT} y calculates y based on t param
 */

/**
 * @returns {MoveFunction}
 */
// eslint-disable-next-line no-unused-vars
function lineFunction(p1, p2) {
  const x0 = p1.x
  const y0 = p1.y
  const dir = direction(p1, p2)
  const a = dir.x
  const b = dir.y
  return { x: t => x0 + a * t, y: t => y0 + b * t }
}

class History {
  constructor(length) {
    this._data = []
    this._data.length = length
    this._head = 0
  }
  last() {
    return this._data[(this._head - 1) % this._data.length]
  }
  push(el) {
    this._data[this._head++ % this._data.length] = el
  }
  *values() {
    for (let i = 0; i < this._data.length; i++) {
      const value = this._data[(this._head + i) % this._data.length]
      if (value !== undefined) yield value
    }
  }
}

export default {
  name: "Home",
  components: { VueP5, Point },
  data() {
    return {
      left: {
        x: 10,
        y: 15,
        r: 200,
      },
      right: {
        x: 450,
        y: 30,
        r: 380,
      },
      sketchSize: {
        width: 0,
        height: 0,
      },
      moveCircleRadius: 50,
      gcode: `G1 X100 Y100 Z0
G1 X200 Z1
G1 Y200
G1 X100
G1 Y100
G1 X100

G1 X200 Y200 Z0
G1 X300 Z1
G1 Y300
G1 X200
G1 Y200
G1 X200 Z0

G1 X100 Y200 Z0
G1 X200 Z1
G1 Y300
G1 X100
G1 Y200
G1 X100 Z0

G1 X200 Y100 Z0
G1 X300 Z1
G1 Y200
G1 X200
G1 Y100
G1 X200 Z0
`,
      moveSpeed: 150,
      isRunningGcode: false,
      isPenActive: false,
    }
  },
  computed: {
    maxR() {
      return distance({ x: 0, y: 0 }, { x: this.sketchSize.width, y: this.sketchSize.height })
    },
    p() {
      const r0 = this.left.r
      const r1 = this.right.r
      const cx0 = this.left.x
      const cy0 = this.left.y
      const cx1 = this.right.x
      const cy1 = this.right.y

      const d = distance(this.left, this.right)

      const a = (r0 ** 2 - r1 ** 2 + d ** 2) / (2 * d)
      const h = Math.sqrt(r0 ** 2 - a ** 2)

      const cx2 = cx0 + (a * (cx1 - cx0)) / d
      const cy2 = cy0 + (a * (cy1 - cy0)) / d

      const x = cx2 - (h * (cy1 - cy0)) / d
      const y = cy2 + (h * (cx1 - cx0)) / d

      return { x, y }
    },
  },
  methods: {
    /**
     * @param {p5} sk
     */
    sketch(sk) {
      const history = new History(500)
      const penHistory = new History(500)
      const drawPoint = ({ x, y }, r, g, b, size = DEFAULT_POINT_SIZE, stroke = true, label = "") => {
        sk.fill(r, g, b)

        if (label) {
          sk.noStroke()
          sk.text(label, x + size * 0.8, y + size * 0.8)
        }

        if (stroke) sk.stroke(0)
        else sk.noStroke()

        sk.circle(x, y, size)
      }

      const drawCircleRadius = (p, radius, r = 0, g = 0, b = 0) => {
        sk.noFill()
        sk.stroke(r, g, b)
        sk.circle(p.x, p.y, radius * 2)
      }

      const drawLine = (p1, p2) => {
        sk.noFill()
        sk.stroke(0)
        sk.line(p1.x, p1.y, p2.x, p2.y)
      }

      sk.setup = () => {
        this.sketchSize.width = this.$refs.sketch.clientWidth
        this.sketchSize.height = this.$refs.sketch.clientHeight
        sk.resizeCanvas(this.sketchSize.width, this.sketchSize.height)
      }

      sk.windowResized = () => {
        try {
          this.sketchSize.width = this.$refs.sketch.clientWidth
          this.sketchSize.height = this.$refs.sketch.clientHeight
          sk.resizeCanvas(this.sketchSize.width, this.sketchSize.height)
          // eslint-disable-next-line no-empty
        } catch {}
      }

      sk.mouseClicked = () => {
        const p = { x: sk.mouseX, y: sk.mouseY }
        if (p.x <= this.sketchSize.width && p.x >= 0 && p.y <= this.sketchSize.height && p.y >= 0) {
          this.moveTo(p, this.moveSpeed)
        }
      }

      sk.draw = () => {
        sk.background(255)
        sk.noFill()
        sk.stroke(0)
        sk.rect(0, 0, sk.width, sk.height)

        if (sk.frameCount % 5 === 0) {
          if (!_.isEqual(this.p, history.last())) history.push({ ...this.p })
          if (this.isRunningGcode) {
            if (this.isPenActive) penHistory.push({ ...this.p })
            else penHistory.push(null)
          }
        }

        sk.stroke(220)
        sk.beginShape()
        for (const p of history.values()) {
          sk.vertex(p.x, p.y)
        }
        sk.endShape()

        sk.stroke(255, 100, 100)
        sk.beginShape()
        for (const p of penHistory.values()) {
          if (p === null) {
            sk.endShape()
            sk.beginShape()
          } else {
            sk.vertex(p.x, p.y)
          }
        }
        sk.endShape()

        drawCircleRadius(this.left, this.left.r)
        drawCircleRadius(this.right, this.right.r)

        drawLine(this.left, this.p)
        drawLine(this.right, this.p)

        drawPoint(this.left, 255, 0, 0, DEFAULT_POINT_SIZE * 1.5, true, "Left")
        drawPoint(this.right, 0, 255, 0, DEFAULT_POINT_SIZE * 1.5, true, "Right")
        drawPoint(this.p, 0, 0, 255, DEFAULT_POINT_SIZE, true, "P")
      }
    },
    /**
     * @param {Number} speed px/s
     */
    async moveTo(p, speed) {
      const time = (distance(this.p, p) / speed) * 1000
      const timeStep = 16.0
      const dt = timeStep / time

      const lineDir = direction(this.p, p)
      const a = lineDir.x
      const b = lineDir.y
      const x0 = this.p.x
      const y0 = this.p.y

      const cx0 = this.left.x
      const cy0 = this.left.y
      const cx1 = this.right.x
      const cy1 = this.right.y

      for (let t = 0.0; t < 1.0; t += dt) {
        const lineX = x0 + a * t
        const lineY = y0 + b * t
        this.left.r = Math.sqrt((lineX - cx0) ** 2 + (lineY - cy0) ** 2)
        this.right.r = Math.sqrt((lineX - cx1) ** 2 + (lineY - cy1) ** 2)

        await new Promise(resolve => setTimeout(resolve, timeStep))
      }
    },

    /**
     * @param {MoveFunction} f calculate x, y based on t
     */
    async moveFunction(f) {
      const time = 1000
      const timeStep = 16.0
      const dt = timeStep / time

      const cx0 = this.left.x
      const cy0 = this.left.y
      const cx1 = this.right.x
      const cy1 = this.right.y

      for (let t = 0.0; t < 1.0; t += dt) {
        const x = f.x(t)
        const y = f.y(t)

        this.left.r = Math.sqrt((x - cx0) ** 2 + (y - cy0) ** 2)
        this.right.r = Math.sqrt((x - cx1) ** 2 + (y - cy1) ** 2)

        await new Promise(resolve => setTimeout(resolve, timeStep))
      }
    },
    async startMovement() {
      this.isRunningGcode = true
      const p = { x: 0.0, y: 0.0 }

      const parseNumber = (command, letter) => {
        const matches = command.match(RegExp(`${letter}([-+]?[0-9]*.?[0-9]+)`))
        if (matches && matches.length === 2) {
          return parseFloat(matches[1])
        }
        return null
      }

      try {
        for (const command of this.gcode.split("\n")) {
          if (!command || command.includes(";")) continue

          const g = parseInt(command.match(/G(\d+)/)[1])
          if (g !== 1) {
            throw Error(`Unknown G command ${g}`)
          }

          const z = parseNumber(command, "Z")
          if (z !== null) {
            this.isPenActive = z > 0
          }

          const x = parseNumber(command, "X")
          if (x !== null) {
            p.x = x
          }

          const y = parseNumber(command, "Y")
          if (y !== null) {
            p.y = y
          }

          await this.moveTo(p, this.moveSpeed)
          if (!this.isRunningGcode) {
            this.isPenActive = false
            return
          }
        }
      } catch (e) {
        console.error("Unsupported G-code:", e)
      }
      this.isPenActive = false
      this.isRunningGcode = false
    },
    async stopMovement() {
      this.isRunningGcode = false
    },
  },
}
</script>

<style lang="scss" scoped>
.full-height {
  height: 100%;
}
.full-width {
  width: 100%;
}
.progress-bar {
  transition: none;
}
</style>
