<template>
  <div class="card card-body p-2">
    <h6><slot></slot></h6>
    <div class="row">
      <div class="col">
        X <span class="badge badge-primary">{{ value.x.toFixed(2) }}</span>
        <input v-if="!readonly" type="range" min="0" :max="size.width" step="1" v-model.number="value.x" />
      </div>
      <div class="col">
        Y <span class="badge badge-primary">{{ value.y.toFixed(2) }}</span>
        <input v-if="!readonly" type="range" min="0" :max="size.height" step="1" v-model.number="value.y" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Point",
  props: {
    value: { type: Object, required: true },
    size: { type: Object, required: true },
    readonly: { type: Boolean, default: false },
  },
  data: function() {
    return {
      actualValue: { ...this.value },
    }
  },
  watch: {
    actualValue() {
      this.$emit("input", this.actualValue)
    },
  },
}
</script>

<style></style>
